<template>
  <div id="app">
    <!-- <div class="content-div"> -->
    <!--<transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">-->
    <router-view/>
    <!--</transition>-->
    <!-- </div> -->
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "app",
  beforeCreate: function() {
    let userCode = this.$urlParam.getUrlKey("code");
    //this.$store.commit("LocalUser", { userCode: userCode });
    if (userCode) {
      //var qs=require('qs');
      // var instance = axios.create({ headers: {'content-type': 'application/x-www-form-urlencoded','access-control-allow-origin': '*' } });
      // instance.post(process.env.API_HOST+"checkLogin",{
      //   userLoginCode: userCode
      // }).then(
      this.$http.post("/ykym-web/checkLogin", JSON.stringify({ userLoginCode: userCode })).then(
        // axios({
        //   method:'get',
        //   baseURL:'http://10.0.20.44:8080',
        //   url:'/checkLogin',
        //   params:{
        //     userLoginCode: userCode
        //   }
        // }).then(
        response => {
          let type = response.data.status;
          this.$store.commit("LocalUser", {
            idTypeList: response.data.idTypeList,userCode: userCode
          });

          if (type == "userLogin") {
            console.log("");
            this.$router.push("/userLogin");
          } else if (type == "userPhoneLogin") {
            console.log("");
            this.$store.commit("LocalUser", {
              userCode: userCode,
              userPhones: response.data.phone
            });
            this.$router.push({
              name: "userPhoneLogin",
              params: {idTypeList:response.data.idTypeList}
            });
          } else if (type == "userIsLogin") {
            if (userCode) {
              this.$router.push({
                name: "Home",
                query:{
                  userCode : userCode
                }
              });
            } else {
              alert("");
              this.$router.push("/userPhoneLogin");
            }
          } else {
            //alert(response.data.status);
            this.$router.push("/Error");
          }
        },
        error => {
          console.log(error);
          this.$router.push("/Error");
        }
      );
    } else {
      this.mui.alert("!!!");
      this.$router.push({
        name: "Error",
        params: {}
      });
    }
  }
};
</script>

<style>
#app {
  margin: 0 !important;
  padding: 0 !important;
  width: 100%;
  height: auto;
}
</style>



