import {//typestypes.js         
	LoadingShow,
	LoadingHide,
	HeaderShow,
	LocalUser,
	SetUserPower,
	SetChildUserInfo
} from './types'

import getters from './getters'//getters.js

const state={
	loading:false,
	header: true,
	user: {},
	userPower:[],
	childUserInfo:{} //
}
const mutations={//
	[LoadingShow](state){
		state.loading=true
	},
	[LoadingHide](state){
		state.loading=false
	},
	[HeaderShow](state){
		state.header = false
	},
	[LocalUser](state, param){
		state.user = param
	},
	[SetUserPower](state, param){
		state.userPower = param
	},
	[SetChildUserInfo](state, param){
		state.childUserInfo = param
	}
}


export default{
	mutations,
	state,
	getters
}


