 {\n        opacity: 1;\n        transform-origin: 100% 0%;\n        transform: scaleX(1);\n    }\n    100% {\n        opacity: 0;\n        transform-origin: 100% 0%;\n        transform: scaleX(.8);\n    }\n}",".button-size(@padding; @font-size; @border-radius) {\n    padding: @padding;\n    font-size: @font-size;\n    border-radius: @border-radius;\n}\n\n.button-color(@color; @background; @border) {\n    color: @color;\n    background-color: @background;\n    border-color: @border;\n    // a inside Button which only work in Chrome\n    // http://stackoverflow.com/a/17253457\n    > a:only-child {\n        color: currentColor;\n        &:after {\n            content: '';\n            position: absolute;\n            top: 0;\n            left: 0;\n            bottom: 0;\n            right: 0;\n            background: transparent;\n        }\n    }\n}\n\n.button-variant(@color; @background; @border) {\n    .button-color(@color; @background; @border);\n\n    &:hover\n    //&:focus\n    {\n        .button-color(tint(@color, 20%); tint(@background, 20%); tint(@border, 20%));\n    }\n    &:active,\n    &.active {\n        .button-color(shade(@color, 5%); shade(@background, 5%); shade(@background, 5%));\n    }\n\n    &.disabled,\n    &[disabled],\n    fieldset[disabled] & {\n        &,\n        &:hover,\n        &:focus,\n        &:active,\n        &.active {\n            .button-color(@btn-disable-color; @btn-disable-bg; @btn-disable-border);\n        }\n    }\n}\n\n.button-group-base(@btnClassName) {\n    position: relative;\n    display: inline-block;\n    vertical-align: middle;\n    > .@{btnClassName} {\n        position: relative;\n        float: left;\n        &:hover,\n        //&:focus,\n        &:active,\n        &.active {\n            z-index: 2;\n        }\n    }\n\n    & .@{btnClassName}-icon-only .ivu-icon {\n        font-size: 14px;\n        position: relative;\n        top: 1px;\n    }\n\n    &-large .@{btnClassName}-icon-only .ivu-icon{\n        font-size: