n-ghost;\n    }\n\n    &-dashed{\n        .btn-dashed;\n    }\n\n    &-text{\n        .btn-text;\n    }\n\n    &-success {\n        .btn-color(@success-color);\n    }\n\n    &-warning {\n        .btn-color(@warning-color);\n    }\n\n    &-error {\n        .btn-color(@error-color);\n    }\n\n    &-info {\n        .btn-color(@info-color);\n    }\n\n    &-circle,\n    &-circle-outline {\n        .btn-circle(@btn-prefix-cls);\n    }\n\n    &:before {\n        position: absolute;\n        top: -1px;\n        left: -1px;\n        bottom: -1px;\n        right: -1px;\n        background: #fff;\n        opacity: 0.35;\n        content: '';\n        border-radius: inherit;\n        z-index: 1;\n        transition: opacity @transition-time;\n        pointer-events: none;\n        display: none;\n    }\n\n    &&-loading {\n        pointer-events: none;\n        position: relative;\n\n        &:before {\n            display: block;\n        }\n    }\n\n    &-group {\n        .btn-group(@btn-prefix-cls);\n    }\n\n    &-group-vertical {\n        .btn-group-vertical(@btn-prefix-cls);\n    }\n}\n",".size(@width; @height) {\n    width: @width;\n    height: @height;\n}\n\n.square(@size) {\n    .size(@size; @size);\n}\n\n// fix chrome 12px bug, support ie\n.iconfont-size-under-12px(@size, @rotate: 0deg) {\n    display: inline-block;\n    @font-scale: unit(@size / @font-size-base);\n    font-size: @font-size-base;\n    font-size: ~\"@{size} \\9\"; // ie8-9\n    transform: scale(@font-scale) rotate(@rotate);\n    :root & {\n        font-size: @font-size-base; // reset ie9 and above\n    }\n}\n",".ivu-affix {\n    position: fixed;\n    z-index: @zindex-affix;\n}\n","@backtop-prefix-cls: ~\"@{css-prefix}back-top\";\n\n.@{backtop-prefix-cls} {\n    z-index: @zindex-back-top;\n    position: fixed;\n    cursor: pointer;\n    display: none;\n\n    &.@{backtop-prefix-cls}-show {\n        display: block;\n    }\n\n    &-inner {\n        background-color: rgba(0,0,0,.6);\n        border-radius: 