ing-right: 32px;\n    }\n    // #554\n    &-hide-icon &-icon-normal + &{\n        padding-right: @input-padding-horizontal;\n    }\n\n    &-wrapper-large &-icon{\n        font-size: 18px;\n        height: @input-height-large;\n        line-height: @input-height-large;\n    }\n    &-wrapper-small &-icon{\n        width: 24px;\n        font-size: 14px;\n        height: @input-height-small;\n        line-height: @input-height-small;\n\n        //+ .@{input-prefix-cls} {\n        //    padding-right: 24px;\n        //}\n    }\n}\n\n.@{input-prefix-cls}-group{\n    .input-group(~\"@{input-prefix-cls}\");\n}\n\n.@{form-item-prefix-cls}-error{\n    .@{input-prefix-cls}{\n        .input-error;\n        &-icon{\n            color: @error-color;\n        }\n    }\n    .@{input-prefix-cls}-group{\n        .input-group-error;\n    }\n    .@{transfer-prefix-cls} {\n        .@{input-prefix-cls} {\n            .input;\n            &-icon{\n                color: @subsidiary-color;\n            }\n        }\n    }\n}\n.@{form-item-prefix-cls}-validating{\n    .@{input-prefix-cls}{\n        &-icon-validate{\n            display: inline-block;\n        }\n        &-icon + .@{input-prefix-cls}{\n            padding-right: 32px;\n        }\n    }\n}\n","@slider-prefix-cls: ~\"@{css-prefix}slider\";\n\n.@{slider-prefix-cls} {\n    line-height: normal;\n    &-wrap{\n        width: 100%;\n        height: @slider-height;\n        margin: @slider-margin;\n        background-color: @border-color-split;\n        border-radius: @btn-border-radius-small;\n        vertical-align: middle;\n        position: relative;\n        cursor: pointer;\n    }\n\n    &-button-wrap{\n        .square(@slider-button-wrap-size);\n        text-align: center;\n        background-color: transparent;\n        position: absolute;\n        top: @slider-button-wrap-offset;\n        transform: translateX(-50%);\n\n        .@{tooltip-prefix-cls} {\n            display: block;\n            user-select: none;\n        }\n    }\n\n    &-button{\n        width: 12px;\n        height: 12px;\n        border: 2px solid @slider-color;\n        border-radius: 50%;\n        background-color: #fff;\n        transition: all @transition-time linear;\n        outline: 0;\n\n        &:focus,\n        &:hover,\n        &-dragging\n        {\n            border-color: @primary-color;\n            transform: scale(1.5);\n        }\n\n        &:hover{\n            cursor: grab;\n        }\n        &-dragging,\n        &-dragging:hover\n        {\n            cursor: grabbing;\n        }\n    }\n\n    &-bar{\n        height: @slider-height;\n        background: @slider-color;\n        border-radius: @btn-border-radius-small;\n        position: absolute;\n    }\n\n    &-stop{\n        position: absolute;\n        .square(@slider-height);\n        border-radius: 50%;\n        background-color: @slider-disabled-color;\n        transform: translateX(-50%);\n    }\n}\n\n.@{slider-prefix-cls}-disabled{\n    cursor: @cursor-disabled;\n\n    .@{slider-prefix-cls}-wrap{\n        background-color: @slider-disabled-color;\n        cursor: @cursor-disabled;\n    }\n    .@{slider-prefix-cls}-bar{\n        background-color: @slider-disabled-color;\n    }\n\n    .@{slider-prefix-cls}-button{\n        border-color: @slider-disabled-color;\n\n        &:hover,\n        &-dragging\n        {\n            border-color: @slider-disabled-color;\n        }\n        &:hover{\n            cursor: @cursor-disabled;\n        }\n        &-dragging,\n        &-dragging:hover\n        {\n            cursor: @cursor-disabled;\n        }\n    }\n}\n\n.@{slider-prefix-cls}-input{\n    .@{slider-prefix-cls}-wrap{\n        width: auto;\n        margin-right: 100px;\n    }\n\n    .@{input-number-prefix-cls}{\n        float: right;\n        margin-top: -14px;\n    }\n}\n","@cascader-prefix-cls: ~\"@{css-prefix}cascader\";\n@cascader-item-prefix-cls: ~\"@{css-prefix}cascader-menu-item\";\n\n.selectDropDown{\n    width: auto;\n    padding: 0;\n    white-space: nowrap;\n    overflow: visible;\n}\n\n.@{cascader-prefix-cls} {\n    //position: relative;\n    line-height: normal;\n\n    &-rel{\n        display: inline-block;\n        width: 100%;\n        position: relative;\n    }\n\n    .@{css-prefix}input{\n        display: block;\n        cursor: pointer;\n    }\n    &-disabled .@{css-prefix}input{\n        cursor: @cursor-disabled;\n    }\n\n    &-label{\n        width: 100%;\n        height: 100%;\n        line-height: 32px;\n        padding: 0 7px;\n        box-sizing: border-box;\n        white-space: nowrap;\n        text-overflow: ellipsis;\n        overflow: hidden;\n        cursor: pointer;\n        font-size: @font-size-small;\n        position: absolute;\n        left: 0;\n        top: 0;\n    }\n    &-size-large &-label{\n        line-height: 36px;\n        font-size: @font-size-base;\n    }\n    &-size-small &-label{\n        line-height: 26px;\n    }\n\n    .@{cascader-prefix-cls}-arrow:nth-of-type(1) {\n        display: none;\n        cursor: pointer;\n    }\n\n    &:hover {\n        .@{cascader-prefix-cls}-arrow:nth-of-type(1) {\n            display: inline-block;\n        }\n    }\n    &-show-clear:hover .@{cascader-prefix-cls}-arrow:nth-of-type(2){\n        display: none;\n    }\n\n    &-arrow {\n        .inner-arrow();\n    }\n    &-visible &-arrow:nth-of-type(2) {\n        transform: rotate(180deg);\n    }\n\n    .@{select-dropdown-prefix-cls} {\n        .selectDropDown();\n    }\n\n    .select-item(@cascader-prefix-cls, @cascader-item-prefix-cls);\n    .ivu-select-item span{\n        color: @error-color;\n    }\n\n    &-dropdown{\n        padding: 5px 0;\n        .@{select-dropdown-prefix-cls}-list{\n            max-height: 190px;\n            box-sizing: border-box;\n            overflow: auto;\n        }\n    }\n\n    &-not-found-tip{\n        padding: 5px 0;\n        text-align: center;\n        color: @btn-disable-color;\n        li:not([class^=ivu-]){\n            list-style: none;\n            margin-bottom: 0;\n        }\n    }\n    &-not-found .@{select-dropdown-prefix-cls} {\n        width: inherit;\n    }\n\n    &-menu{\n        display: inline-block;\n        min-width: 100px;\n        height: 180px;\n        margin: 0;\n        padding: 5px 0 !important;\n        vertical-align: top;\n        list-style: none;\n        border-right: 1px solid @border-color-split;\n        overflow: auto;\n\n        &:first-child {\n\n        }\n        &:last-child {\n            border-right-color: transparent;\n            margin-right: -1px;\n        }\n        &:only-child {\n\n        }\n\n        & &-item{\n            position: relative;\n            padding-right: 24px;\n            transition: all @transition-time @ease-in-out;\n\n            i{\n                font-size: @font-size-small;\n                position: absolute;\n                right: 15px;\n                top: 50%;\n                margin-top: -6px;\n            }\n\n            &-active{\n                background-color: @background-color-select-hover;\n                color: @primary-color;\n            }\n        }\n    }\n}\n.@{cascader-prefix-cls}-transfer{\n    z-index: @zindex-transfer;\n    .selectDropDown();\n    .select-item(@cascader-prefix-cls, @cascader-item-prefix-cls);\n    .ivu-select-item span{\n        color: @error-color;\n    }\n    .@{cascader-prefix-cls} {\n        &-menu{\n            &-item{\n                padding-right: 24px;\n                transition: all @transition-time @ease-in-out;\n                &-active{\n                    background-color: @background-color-select-hover;\n                    color: @primary-color;\n                }\n            }\n        }\n    }\n}\n\n.@{form-item-prefix-cls}-error{\n    .@{cascader-prefix-cls} {\n        &-arrow{\n            color: @error-color;\n        }\n    }\n}","@transfer-prefix-cls: ~\"@{css-prefix}transfer\";\n@transfer-item-prefix-cls: ~\"@{css-prefix}transfer-list-content-item\";\