align: center;\n        }\n        &-right{\n            text-align: right;\n        }\n    }\n\n    & table{\n        //width: 100%;\n        table-layout: fixed;\n    }\n    &-border{\n        th,td{\n            border-right: 1px solid @border-color-split;\n        }\n    }\n    &-cell{\n        padding-left: 18px;\n        padding-right: 18px;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        white-space: normal;\n        word-break: break-all;\n        box-sizing: border-box;\n\n        &-ellipsis {\n            word-break: keep-all;\n            white-space: nowrap;\n            overflow: hidden;\n            text-overflow: ellipsis;\n        }\n\n        &-with-expand{\n            height: 47px;\n            line-height: 47px;\n            padding: 0;\n            text-align: center;\n        }\n\n        &-expand{\n            cursor: pointer;\n            transition: transform @transition-time @ease-in-out;\n            i{\n                font-size: @font-size-base;\n            }\n            &-expanded{\n                transform: rotate(90deg);\n            }\n        }\n        &-sort{\n            cursor: pointer;\n            user-select: none;\n        }\n    }\n    &-hidden{\n        visibility: hidden;\n    }\n    th &-cell{\n        display: inline-block;\n        //position: relative;\n        word-wrap: normal;\n        vertical-align: middle;\n    }\n\n    td&-expanded-cell{\n        padding: 20px 50px;\n        background: @table-thead-bg;\n    }\n\n    &-stripe &-body,\n    &-stripe &-fixed-body\n    {\n        tr:nth-child(2n) {\n            td{\n                background-color: @table-td-stripe-bg;\n            }\n        }\n        // #1380\n        tr.@{table-prefix-cls}-row-hover{\n            td{\n                background-color: @table-td-hover-bg;\n            }\n        }\n    }\n\n    tr&-row-hover{\n        td{\n            background-color: @table-td-hover-bg;\n        }\n    }\n\n    &-large {\n        font-size: @font-size-base;\n        th{\n            height: 48px;\n        }\n        td{\n            height: 60px;\n        }\n        &-title, &-footer{\n            height: 60px;\n            line-height: 60px;\n        }\n        .@{table-prefix-cls}-cell-with-expand{\n            height: 59px;\n            line-height: 59px;\n            i{\n                font-size: @font-size-base+2;\n            }\n        }\n    }\n\n    &-small{\n        th{\n            height: 32px;\n        }\n        td{\n            height: 40px;\n        }\n        &-title, &-footer{\n            height: 40px;\n            line-height: 40px;\n        }\n        .@{table-prefix-cls}-cell-with-expand{\n            height: 39px;\n            line-height: 39px;\n        }\n    }\n\n    &-row-highlight,\n    tr&-row-highlight&-row-hover,\n    &-stripe &-body tr&-row-highlight:nth-child(2n),\n    &-stripe &-fixed-body tr&-row-highlight:nth-child(2n)\n    {\n        td{\n            background-color: @table-td-highlight-bg;\n        }\n    }\n\n    &-fixed, &-fixed-right{\n        position: absolute;\n        top: 0;\n        left: 0;\n        box-shadow: 2px 0 6px -2px rgba(0, 0, 0, 0.2);\n\n        &::before {\n            content: '';\n            width: 100%;\n            height: 1px;\n            background-color: @border-color-base;\n            position: absolute;\n            left: 0;\n            bottom: 0;\n            z-index: 4;\n        }\n    }\n    &-fixed-right{\n        top: 0;\n        left: auto;\n        right: 0;\n        box-shadow: -2px 0 6px -2px rgba(0, 0, 0, 0.2);\n    }\n    &-fixed-right-header{\n        position: absolute;\n        top: -1px;\n        right: 0;\n        background-color: @table-thead-bg;\n        border-top: 1px solid @border-color-base;\n        border-bottom: 1px solid @border-color-split;\n    }\n    &-fixed-header{\n        overflow: hidden;\n        &-with-empty{\n            .@{table-prefix-cls}-hidden{\n                .@{table-prefix-cls