// vue-clivue
Vue.config.productionTip = false;
// iView
Vue.use(iView);
function res(response, callback) {
  var res = {
    resEntity: response.data,
    status: response.status
  };
  if (res.status === 200) {
    if (res.resEntity.code === 1003) {
      iView.Message.error(res.resEntity.msg);
      router.push('/login');
    }
  } else {
    iView.Message.error(res.status);
  }
}

// axios---
axios.interceptors.request.use(function(config) {
  // -loding
  store.dispatch('loadingShow');
  // Indicator.open('...');
  // 
  return config;
}, function(error) {
  // -loding
  store.dispatch('loadingHide');
  // Indicator.close();
  // 
  return Promise.reject(error);
});

// axios---
axios.interceptors.response.use(function(response) {
  res(response);
  // console.log('http');
  // console.log(response);
  // -loding
   store.dispatch('loadingHide');
  // Indicator.close();
  // 
  return response;
}, function(error) {
  // -loding
  store.dispatch('loadingHide');
  // Indicator.close();
  // iView.Message.error('');
  // 
  return Promise.reject(error);
});
