import Vue from 'vue';
import Router from 'vue-router';
// import Header from '@/views/Header/Header.vue';

Vue.use(Router);

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/Home',
      name: 'Home',
      // component: Home,
      component: resolve => require(['@/views/Home/Home.vue'], resolve),
      meta: {
        title: 'comparator service'
      }
    },
    {
      path: '/ClaimListInfo',
      name: 'ClaimListInfo',
      component: resolve => require(['@/views/PolicyReal/ClaimInfo/claimListInfo.vue'], resolve)
    },   
    {
        path: '/PolicyReal',
        name: 'PolicyReal',
        component: resolve => require(['@/views/PolicyReal/policyList.vue'],resolve),
        meta: {
            title: ''
        }
    },
    {
      path:'/policyInfo',
      name: 'policyInfo',
      component: resolve => require(['@/views/PolicyReal/policyInfo.vue'],resolve),
        meta: {
            title: ''
        }
    },
    {
      path: '/userLogin',
      name: 'UserLogin',
      // component: Account,
      component: resolve => require(['@/views/FirstHomeVo/home/UserLogin.vue'], resolve)
    },
    {
      path: '/identifying',
      name: 'Identifying',
      // component: Account,
      component: resolve => require(['@/views/FirstHomeVo/home/Identifying.vue'], resolve)
    },
    {
      path: '/userPhoneLogin',
      name: 'userPhoneLogin',
      // component: Account,
      component: resolve => require(['@/views/FirstHomeVo/home/userPhoneLogin.vue'], resolve)
    },
    {
      path: '/updataUserPhone',
      name: 'UpdataUserPhone',
      // component: Account,
      component: resolve => require(['@/views/FirstHomeVo/home/UpdataUserPhone.vue'], resolve)
    },
    {
      path: '/Error',
      name: 'Error',
      component: resolve => require(['@/views/Test/Test.vue'], resolve)
    }
  ]
});



